import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { Layout, PageHeader, Form, message, Button, InputNumber, Input, Alert } from 'antd';
import Apis from '@/apis'
import { Selector, } from '@/components';
const { Content } = Layout;

@inject(({globalStore})=>({
  getSetting: globalStore.getSetting,
  setting: globalStore.setting,
  permissions: globalStore.permissions,
  formItemLayout: globalStore.formItemLayout,
  formTailLayout: globalStore.formTailLayout,
}))
@observer
class EditView extends Component {
  formRef = React.createRef();
  state = {
    loading: true,
  };

  handleSubmit = values => {
    const { setting={}, getSetting } = this.props
    // if(values.currentReader) values.currentReader = values.currentReader.split(',')[0]
    Apis.settingUpdate({
      _id: setting._id, ...values,
    }).then(res => {
      if(res){
        message.success('提交成功!')
        setTimeout(() => {
          getSetting()
        }, 1000);
      }else{
        message.error('提交失败!')
      }
    })
  };
  handleOpenReader = val => {
    // console.warn(val);
  }

  render() {
    const { formItemLayout, formTailLayout, setting={}, permissions } = this.props
    return (
      <Content>
        <PageHeader
          ghost={true}
          className="page-title"
          onBack={false}
          title={`系统设置`}
        />
        {permissions['manageSetting/update'] ? 
        <Content>
          <div className="app-content">
         <Form {...formItemLayout} onFinish={this.handleSubmit} initialValues={setting} ref={this.formRef} className="app-form">
            <Form.Item label="加密机IP" name="encryptionerIp">
              <Input />
            </Form.Item>
            <Form.Item label="加密机端口" name="encryptionerPort">
              <Input />
            </Form.Item>
            <Form.Item label="当前读写器" name="currentReader">
              <Selector 
                api="dictionaryOptions" 
                params={{_id: '60181a67f003ed1289377022'}}
                titlepropname="key"
                valuepropname="value"
              />
            </Form.Item>
            <Form.Item label="控件ClassId" name="ocxClassId">
              <Input />
            </Form.Item>
            {/* <Form.Item label="读写器状态" name="openCloseReader" valuePropName="checked">
              <Switch checkedChildren="开启" unCheckedChildren="关闭" onChange={this.handleOpenReader} />
            </Form.Item> */}
            <Form.Item label="最大上传文件大小(KB)" name="uploadMax">
              <InputNumber />
            </Form.Item>
            <Form.Item label="每页条目数" name="pageSize" help="默认20条">
              <InputNumber />
            </Form.Item>
            {
              permissions['manageSetting/update'] &&
            <Form.Item {...formTailLayout}>
              <Button type="primary" htmlType="submit" style={{marginRight: 10}}>保存</Button>
            </Form.Item>
            }
          </Form>
          </div>
        </Content>
        : <Alert
          message="信息提示"
          description="没有权限查看内容, 请联系管理员."
          type="warning"
          showIcon
          style={{margin: '0 20px'}}
        />}
      </Content>
    )
  }
};

export default EditView;
